<template>
  <div class="home">
    <div class="account">
      <label>Current account</label>
      <select v-model="currentAccount" v-on:change="onChange">
          <option v-for="account in accounts" :key="account.id" :value="account">
            {{ account.id }}
          </option>
      </select>
      <button v-on:click="showTransactionList">
          <p v-if="transactions.length == 0">Show transaction list</p>
          <p v-else>Hide transaction list</p>
      </button>
    </div>
    <div class="transaction-data">
      <h2>Transaction data</h2>
      <div class="form-fields">
        <label for="amount">Amount</label>
        <input type="number" v-model="transaction.amount"
                v-validate.initial="'required'" name="amount"/>
        <div class="errors" v-show="errors.has('amount')">{{ errors.first('amount') }}</div>
      </div>
      <div class="form-fields">
        <label for="reason">Reason</label>
        <input type="text" v-model="transaction.reason"
                v-validate.initial="'required'" name="reason"/>
        <div class="errors" v-show="errors.has('reason')">{{ errors.first('reason') }}</div>
      </div>
      <div>
        <button v-on:click="saveTransaction">
              Save transaction
        </button>
      </div>
    </div>
    <div class="transaction-list" v-if="transactions.length != 0">
        <div id="line">
          <div class="transaction-fields">Type</div>
          <div class="transaction-fields">Amount</div>
          <div>Reason</div>
        </div>
        <div id="line" v-for="item in transactions" :key="item.id" :class="item.type">
            <div class="transaction-fields">{{item.type}}</div>
            <div class="transaction-fields">{{item.amount}}</div>
            <div>{{item.reason}}</div>
        </div>
    </div>
  </div>
</template>

<script>
import axios from 'axios'

export default {
  name: 'Home',
  data () {
    return {
      accounts: [],
      transaction: {},
      currentAccount: null,
      transactions: [{amount: 50, reason: 'qeqwe', type: 'REJECTED'},
        {amount: -50, reason: 'qeqwe', type: 'DEBIT'}]
    }
  },
  beforeCreate () {
    axios.get('/get-bankAccounts')
      .then((response) => {
        this.accounts = response.data
        this.currentAccount = this.accounts[0]
      })
  },
  methods: {
    saveTransaction: function (event) {
      if (this.errors.items.length > 0) {
        alert('Please correct the transaction fields error(s).')
        return
      }
      this.transaction.bankAccount = this.currentAccount
      axios.post('/save-transaction', this.transaction)
        .then((response) => {
          if (response.data.type === 'REJECTED') {
            alert('Transaction was rejected')
          } else {
            alert('Transaction was accounted')
            this.transaction = {}
          }
        })
    },
    showTransactionList: function (event) {
      if (this.transactions.length > 0) {
        this.transactions = []
        return
      }
      this.getTransactionList()
    },
    onChange: function (event) {
      alert('Account was changed')
      if (this.transactions.length > 0) {
        this.getTransactionList()
      }
    },
    getTransactionList: function () {
      axios.get('/get-transactions', {
        params: {
          id: this.currentAccount.id
        }
      })
        .then((response) => {
          this.transactions = response.data
          if (this.transactions.length === 0) {
            alert('There are no transactions on this bank account yet.')
          }
        })
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  #line{
    border: solid 1px #000000;
    height: 20px;
    margin: 2px;
  }

  .account{
    width: 300px;
    height:50px;
    border: 1px solid green;
  }

  .transaction-data{
    width: 300px;
    height: 200px;
  }

  .form-fields{
    margin: 12px;
  }

  .errors{
    color: red;
  }

  .transaction-fields{
    width: 20%;
    float: left;
  }

  .transaction-list{
     width: 50%;
  }

  .REJECTED {
      background : red;
  }

  .DEBIT{
      background: green;
  }

  .CREDIT{
        background: yellow;
    }

  .border, .transaction-data {
    border: 1px solid green;
  }
</style>



